import React from 'react'
import { useFormikContext } from 'formik';
import { graphql, navigate } from 'gatsby';
import { max } from 'lodash';

import Text from '../components/Text';
import Container from '../components/Container';

import Layout from '../containers/Layout'

const cityName = {
  france: '法國尼斯',
  germany: '德國慕尼黑',
  hawaii: '美國夏威夷',
  iceland: '冰島雷克雅維克',
  netherlands: '荷蘭阿姆斯特丹',
  new_york: '美國紐約',
  russia: '俄羅斯莫斯科 ',
  spain: '西班牙伊比薩島',
  sweden: '瑞典斯德哥爾摩',
  turkey: '土耳其伊斯坦堡',
}

const Result = ({ data, pageContext: { id, questions } }) => {
  const { values } = useFormikContext()
  if(values.answers.length < questions.length) {
    navigate(`/q/1`)
    return null
  }
  const percent = (values.points * 100) / data.jsonJson[id].reduce((res, scores) => {
    res = res + max(scores)
    return res
  }, 0)
  console.log(percent)
  return (
    <Layout title={id}>
      <Container>
        <Text>你是 {Math.floor(percent)}% 的不羈探險家</Text>
        <Text>適合去的城市 {cityName[id]}</Text>
        <Text>不用梁靜茹特別給，我自己就有勇氣冒險</Text>
        <Text fontSize="0.75em">勇敢冒險已經是你的日常，你實在無法想像一成不變、瞻前顧後的無聊日子，探索不同的風景才是人生的意義！神秘又充滿異域風情的伊斯坦堡，當然說走就走啊。</Text>
      </Container>
    </Layout>
  )
}

export default Result

export const query = graphql`
  query ScoreQuery {
    jsonJson {
      france
      germany
      hawaii
      iceland
      netherlands
      new_york
      russia
      spain
      sweden
      turkey
    }
  }
`
